import DisplayUrl from '../../../PublishCommonComponents/DisplayUrl';
import { DisplayURL, UGC_SUITE_WALL_PREFIX } from '../../../../../Services/Api'
const CastUrl = ({ data }) => {
    return <div className="card text-decoration-none">
        <div className="card-body p-lg-7">
            <div className="m-auto">
                <div className="embed_code">
                    <h5 className="mb-5 fw-bolder">Steps To Display Social Wall With Wireless Connection</h5>
                    <h6>Step 1 : Copy The Display Wall URL</h6>
                    <DisplayUrl url={`${DisplayURL}${UGC_SUITE_WALL_PREFIX}${data && Object.keys(data).length > 0 ? data.wallId : null}`} />
                    <h6 className="mb-5">Step 2 : Cast Display Wall with Wireless connection</h6>
                    <ul className="list-unstyled list-points fs-7">
                        <li className="mb-3">Mirror/cast your screen through Google CHROMECAST, MIRACAST Wireless Display, or APPLE TV 4K.</li>
                        <li className="mb-3">Launch your Tagbox URL in the browser and don't forget to <span className="fw-bold">TURN ON</span> the Fullscreen display.</li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
}

export default CastUrl;